<template>
  <div>
      <button @click="geoloc">Ваши координаты!</button>
      <h3 >{{coords}}</h3>
    <h1>Загадка</h1>
    
    <p>{{mem[check()]}}</p>
  </div>
</template>

<script>
    
     
export default {
    data(){return{
    mem:
['Не верная геопозиция','Финский снег',
'S  в честь профессии',
'Вилка или (Славное Молоко)',
'"Чем меньше женщину мы любим, тем больше нравимся мы ей"  + картинка замкА',
'Подарок от Риги',
'Раньше ЕГО имя носил Петрозаводский Государственный Университет, а сейчас в честь НЕГО названы улицы в Москве, Харькове, Астрахани и Алма-Ате. Найдите и в нашем городе памятное сооружение в ЕГО честь.',
'Петрозаводский Лувр',
'Местный мыслитель'],
    coords:{
        latitude:undefined,
        longitude:undefined
    }}},
    created(){this.geoloc()},
    methods:{
        check(){
            let lat = this.coords.latitude;
            let lon = this.coords.longitude;
            if ((61.792<lat && lat<61.793) &&  (34.356<lon && lon<34.357)){
                return 8
            }
            else if ((61.7928<lat && lat<61.7935) &&  (34.3628<lon && lon<34.363)){
                return 1
            }
            else if ((61.795<lat && lat<61.7955) &&  (34.3628<lon && lon<34.3632)){
                return 2
            }
            else if ((61.798<lat && lat<61.7985) &&  (34.368<lon && lon<34.3685)){
                return 3
            }
            else if ((61.796<lat && lat<61.79685) &&  (34.373<lon && lon<34.3735)){
                return 4
            }
            else if ((61.794<lat && lat<61.7945) &&  (34.375<lon && lon<34.3755)){
                return 5
            }
            else if ((61.792<lat && lat<61.7925) &&  (34.368<lon && lon<34.36885)){
                return 6
            }
            else if ((61.790<lat && lat<61.7905) &&  (34.358<lon && lon<34.3585)){
                return 7
            }else{return 0}
            
         },
        setdata(data){
            const coord = data.coords;
            this.coords.longitude=coord.longitude
            this.coords.latitude=coord.latitude
        },
        geoloc(){
            navigator.geolocation.getCurrentPosition(this.setdata);
        }
    }
}



</script>

